@import "../../../css/module/fn";
@import "../../../css/module/color";


#slider {
	margin-top: p2m(40);
	width: 100%;
	height: p2m(840);
	
	.swiper-slide {
		text-align: center;
		font-size: 18px;
		background: #fff;
		width: p2m(530);
		height: p2m(810);
		border-radius: p2m(15);
		
		.cont{
			margin: p2m(15);
			height: p2m(690);
			overflow-y: hidden;
			
			img{
				background-color: #eee;
				width: 100%;
				height: p2m(405);
			}
			
			h1{
				font-size: p2m(28);
				margin: 1em ;
				color: #000;
				text:{
					align: left;
					transform: uppercase;
				}
				
				.pointer{
					display: inline-block;
					border-radius: p2m(10);
					background-color: $red;
					height: p2m(34);
					line-height: p2m(34);
					padding: 0 0.6em;
					font-size: p2m(16);
					color: #fff;
					float: right;
					.mui-icon{
						font-size: p2m(12);
					}
				}
			}
			p{
				color: $font-black;
				font-size: p2m(22);
				margin: p2m(28);
				text:{
					align: left;
					//transform: uppercase;
				}
				display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
				
				-webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
				
				-webkit-line-clamp: 3; /** 显示的行数 **/
				
				overflow: hidden;  /** 隐藏超出的内容 **/
			}
		}
		
		.contFoot{
			height: p2m(90);
			background-color: #eee;
			line-height: p2m(90);
			border-bottom:{
				left-radius: p2m(15);
				right-radius: p2m(15);
			}
			color: #888;
			font-size: p2m(20);
			.mui-icon-extra{
				font-size: p2m(20);
			}
		}
		
	}
	
	/*.swiper-slide-active{
		width: p2m(530);
		height: p2m(780);
	}*/
}


.rate{
	margin: p2m(46) auto 0;
	width: p2m(530);
	.rd{
		width: p2m(175);
		height: p2m(175);
		border-radius: p2m(200);
		background-color: #fff;
		
		box-shadow: 0 0 10px 0 rgba(0,0,0,0.3);
		
		&#like{
			float: left;
			background: {
				image: url("ico-like.png");
				repeat: no-repeat;
				position: center center;
				size: p2m(95) p2m(81);
			};
		}
		&#unlike{
			float: right;
			background: {
				image: url("ico-hate.png");
				repeat: no-repeat;
				position: center center;
				size: p2m(95) p2m(81);
			};
		}
	}
}

.mui-content{
	height: p2m(1000);
	background:{
		image: url("bg-bottom.png");
		repeat: no-repeat;
		position: center bottom;
		size: p2m(550) p2m(64);
	}
}


.resultContainer{
	background-color: #fff;
	border-radius: 20px;
	padding: p2m(70);
	text-align: center;
	@include centered(590, 960);
	
	h1{
		text-align: center;
		text-transform: uppercase;
		font-size: p2m(50);
		line-height: p2m(70);
		width: p2m(330);
		margin: 0 auto;
		border-bottom: 5px double $red;
	}
	
	.imgRs{
		display: block;
		width: p2m(200);
		height: p2m(180);
		margin: p2m(20) auto;
	}
	
	.res{
		font-size: p2m(70);
		line-height: 1.5em;
	}
}